  width: 30%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
} */


.widget .weatherInfo .description .weatherCondition {
  text-transform: uppercase;
  font-size: 24px;
  font-weight: 200;
}
@media screen and (max-width: 500px) {
  #root {
    width: 100vw;
  }

  .widget {
    width: 93%;
    margin: auto;
  }

  /* .widget .weatherInfo {
    flex: 0 0 65%;
  } */
  .widget .date {
    flex: 0 0 43%;
    font-size: 21px;
    font-weight: 700;
  }

  .widget .weatherInfo {
    flex: 0 0 57%;
  }

  .widget .weatherInfo .description .weatherCondition {
    font-size: 24px;
    padding-right: 1px;
  }

  .widget .weatherInfo .temperature {
    flex: 0 0 40%;
    font-size: 36px;
  }
  .description{
    margin-left: 2px !important
  }
  .temperature span{
    margin-left: 10px !important
  }
}